@font-face {
  font-family: 'OkineSans';
  src: local('OkineSans'), url(/static/media/OkineSans-Regular.ee4edf5d.otf) format('truetype');
}
body {
  margin: 0;
  font-family: 'OkineSans-Regular', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.TitleHeader {
  min-height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-size: 40px;
  color: white;
  font-family: 'OkineSans-Thin', sans-serif;
  font-weight: 300;
  width: 100%;
}

.Header {
  height: 100%;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  color: white;
  font-family: 'OkineSans-Thin', sans-serif;
  font-weight: 300;
}

.HeaderLinks {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.body {
  padding-top: 20px;
  background-color: white;
  color: black;
  text-align: center;
}

.content {
  text-align: left;
  display: inline-block;
  width: 800px;
  margin-left:5%;
  margin-right: 5%;
}

.aboutBody {
  display: flex;
  flex-direction: row;
}

.aboutImage {
  padding-left: 3%;
  width: 300px;
  height: 300px;
}

a {
  font-size: 15px;
  color: white;
  font-family: 'OkineSans-Regular', sans-serif;
  font-weight: 600;
  text-decoration: none;
  padding: 5px;
  margin-left: 20px;
  margin-right: 20px;
}

a:hover {
  color: lightgrey;
}


p {
  margin: 0;
  font-family: 'OkineSans-Regular', sans-serif;
  font-weight: 400;
    font-size: 16px;
}
.footer{
  padding-top: 3px;
  padding-bottom: 3px;
  background-color: #282c34;
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
   
  @media (max-width: 1000px) {
    padding: 70px 30px;
  }
}

.footerImage {
  max-width: 20px;
  max-height: 20px;
}
